import { useEffect, useRef, useState } from "react";
import { containsVideo, containsVideoID, createSearchParams, Player, setNewVideo } from "@/app/utils";
import { usePlayer, useMedia, useUser, usePlaylist } from "@/app/store";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import { useClickOutside } from "@/app/hooks";

export default function Playlist() {
    const { user, isPlaylistActive } = useUser();
    const clickRef = useRef<HTMLDivElement | null>(null);
    const [tooltipVisible, setTooltipVisible] = useState(false);
    const { setYoutubeVideo, setTiktokVideo, youtubeVideo, tiktokVideo, soundCloudVideo, setSliderValue, duration } = usePlayer();
    const [activeVideoIndex, setActiveVideoIndex] = useState<number | null>(null);
    const {
        removeFromPlaylist,
        removeVideo,
        playlistID,
        playlistTitle,
        videos,
        setVideos,
        setCurrentVideo,
        currentVideo,
        playNextVideo,
        playSelectedVideo,
    } = useMedia();

    const videoPlaying = currentVideo || youtubeVideo || tiktokVideo || soundCloudVideo;

    useEffect(() => {
        if (videoPlaying) {
            const { video } = containsVideoID(videos, videoPlaying.videoID);
            if (video && video.videoID !== currentVideo?.videoID) {
                setCurrentVideo(video);
            }
        }
    }, [videoPlaying, videos, setCurrentVideo]);

    useEffect(() => {
        if (user) {
            const fetchPlaylist = async () => {
                // Fetch the playlist data
                await useMedia.getState().fetchPlaylistData(user);
                const fetchedVideos = useMedia.getState().playlistVideos;

                // Function to remove duplicates and handle removal from the playlist
                const uniqueVideos = Array.from(new Map(fetchedVideos.map((video) => [video?.videoID, video])).values());

                // Remove duplicates from the playlist
                for (const video of fetchedVideos) {
                    if (!uniqueVideos.some((uniqueVideo) => uniqueVideo?.videoID === video?.videoID)) {
                        // Video is a duplicate; remove it from the playlist
                        await removeFromPlaylist(video, user.uid);
                    }
                }

                setVideos(uniqueVideos); // Set the unique playlist videos

                const video = youtubeVideo || tiktokVideo || soundCloudVideo;
                if (!video) return;

                const { index } = containsVideoID(uniqueVideos, video.videoID); // Check in unique videos
                if (index === null) return;

                setCurrentVideo(uniqueVideos[index]);
            };

            fetchPlaylist();
        }
    }, [user]);

    useClickOutside(clickRef, setTooltipVisible, ["click"], () => {
        console.log("Clicked outside");
    });

    const onVideoClick = async (url: string | undefined, index: number) => {
        if (!videoPlaying || !url || url === videoPlaying.url) return;
        try {
            new URL(url);
            playSelectedVideo(index, setTiktokVideo, setYoutubeVideo, setSliderValue, isPlaylistActive, playlistID);
        } catch (error) {
            const err = (error as Error).message;
            console.error(err);
        }
    };

    const handleTooltipClick = (index: number) => {
        if (activeVideoIndex === index) {
            setTooltipVisible(false);
            setActiveVideoIndex(null);
        } else {
            setTooltipVisible(true);
            setActiveVideoIndex(index);
        }
    };

    const handleRemoveVideo = async (videoIndex: number) => {
        const videoToRemove = videos[videoIndex];

        if (isPlaylistActive && user) {
            await removeFromPlaylist(videoToRemove, user?.uid);
        } else {
            removeVideo(videoToRemove);
        }
        setTooltipVisible(false);
    };

    return (
        <div className="h-[850px] w-full pt-2 p-4 flex flex-col justify-start bg-transparent rounded-lg " style={{ minWidth: "500px" }}>
            <h2 className="text-xl text-center font-semibold bg-transparent mb-4 mt-4">{isPlaylistActive ? playlistTitle : "Queue"}</h2>
            <ul className="space-y-2">
                {videos &&
                    Array.isArray(videos) &&
                    videos.map((video: Player, index: number) => (
                        <li
                            key={index}
                            className={`w-full cursor-pointer p-2 rounded-md flex items-center space-x-4 justify-between
                            ${videoPlaying?.url === video?.url ? "bg-stone-300 " : "bg-stone-50 bg-opacity-40 hover:bg-gray-300"}`}
                            onClick={() => onVideoClick(video?.url, index)}
                        >
                            {/* Video Thumbnail */}
                            <img
                                src={video?.thumbnail || "https://i.imgur.com/Dr1IZF3.jpeg"}
                                alt={video?.title}
                                className="w-16 h-16 object-cover rounded-md"
                            />

                            {/* Video Title and Channel */}
                            <div className="relative flex flex-col w-full">
                                <span className="text-sm font-semibold">{video?.title}</span>
                                <span className="text-xs text-gray-500">{video?.creator || video?.channel}</span>
                            </div>

                            {/* Action Icon */}
                            <div
                                className="relative pr-2"
                                onClick={(e) => {
                                    e.stopPropagation(); // Prevents click from closing tooltip immediately
                                    handleTooltipClick(index); // Toggle tooltip visibility for the clicked video
                                }}
                            >
                                <MoreVertIcon className="w-5 h-5 text-gray-600 hover:text-gray-900" />

                                {/* Tooltip */}
                                {tooltipVisible && activeVideoIndex === index && (
                                    <div
                                        ref={clickRef}
                                        className="absolute right-0 z-10 w-40 bg-white border border-gray-300 rounded-md shadow-lg mt-2"
                                    >
                                        <button
                                            className="block w-full px-4 py-2 text-left text-sm hover:bg-gray-200"
                                            onClick={(e) => {
                                                e.stopPropagation(); // Stop propagation for button click only
                                                handleRemoveVideo(index); // Call remove function
                                            }}
                                        >
                                            {isPlaylistActive ? "Remove from Playlist" : "Remove from Queue"}
                                        </button>
                                        <button
                                            className="block w-full px-4 py-2 text-left text-sm hover:bg-gray-200"
                                            onClick={(e) => {
                                                e.stopPropagation(); // Stop propagation for button click only
                                                // handleDownload(video.url); // Call download function
                                            }}
                                        >
                                            Download
                                        </button>
                                    </div>
                                )}
                            </div>
                        </li>
                    ))}
            </ul>
        </div>
    );
}
