import React, { useEffect, useRef, useState } from "react";
import { useMedia, usePlayer, useUser } from "@/app/store";
import { createSearchParams, ITikTokEmbed, Player } from "@/app/utils";

export default function TiktokPlayer({ tiktokVideo }: { tiktokVideo: Player }) {
    if (!tiktokVideo) return null;

    const { setYoutubeVideo, setTiktokVideo, setSliderValue } = usePlayer();
    const { loopEnabled, playNextVideo, playlistID } = useMedia();
    const { isPlaylistActive } = useUser();
    const videoID = tiktokVideo.videoID.split("+")[1];
    const iframeRef = useRef<HTMLIFrameElement | null>(null);
    const [hasEnded, setHasEnded] = useState(false);

    useEffect(() => {
        const handlePlayerMessage = (event: MessageEvent) => {
            if (event.data?.["x-tiktok-player"]) {
                if (event.data.type === "onPlayerReady") {
                    iframeRef.current?.contentWindow?.postMessage({ type: "unMute", "x-tiktok-player": true }, "*");
                }
                if (event.data.type === "onStateChange" && event.data.value === 0) {
                    setHasEnded(true);
                }
            }
        };

        window.addEventListener("message", handlePlayerMessage);
        return () => {
            window.removeEventListener("message", handlePlayerMessage);
        };
    }, []);

    useEffect(() => {
        if (hasEnded && !loopEnabled) {
            playNextVideo(setTiktokVideo, setYoutubeVideo, setSliderValue, isPlaylistActive, playlistID, createSearchParams);
        }
    }, [hasEnded]);

    return (
        <div className="bg-gray-700 rounded-lg h-[700px] w-[1000px]">
            <iframe
                ref={iframeRef}
                src={`https://www.tiktok.com/player/v1/${videoID}?autoplay=1&mute=0&loop=${loopEnabled ? 1 : 0}&volume_control=1&fullscreen_button=1`}
                allow="fullscreen; autoplay"
                width="100%"
                height="100%"
                title="TikTok Video"
                style={{ border: "none" }}
            />
            {hasEnded && <div className="text-white">The video has ended.</div>}
        </div>
    );
}
