import Link from "next/link";

export default function Tooltips() {
    return (
        <Link href="/login">
            <div className="absolute left-1/2 top-[550px] transform -translate-x-1/5 z-10 w-64 p-4 bg-white shadow-lg border border-stone-900 rounded-lg">
                <p className="text-sm text-gray-700">Want to watch this again later? Sign in to add this video to a playlist.</p>
                <span className="mt-4 inline-block text-blue-600 hover:text-blue-700 font-semibold">Sign in</span>
            </div>
        </Link>
    );
}
