import { toast } from "sonner";
import Tooltips from "./Tooltips";
import { useClickOutside } from "@/app/hooks";
import React, { useRef, useState } from "react";
import styled, { keyframes } from "styled-components";
import { usePlayer, useMedia, useUser } from "@/app/store";
import QueueMusicIcon from "@mui/icons-material/QueueMusic";
import { containsVideo, containsVideoID, createVideoObject, processVideoAddition } from "@/app/utils";

const PlaylistButton = () => {
    const [pressed, setPressed] = useState<boolean>(false)
    const { tiktokVideo, soundCloudVideo, youtubeVideo, duration } = usePlayer();
    const { addToPlaylist, clearVideos, videos, addVideo, clearPlaylist, playlistID, playlistVideos, setCurrentVideo, clearQueue } = useMedia();
    const { user, togglePlaylist } = useUser();
    const [showTooltips, setShowTooltips] = useState<boolean>(false);
    const clickRef = useRef<HTMLDivElement | null>(null);

    useClickOutside(clickRef, setShowTooltips, ["click"], () => {
        console.log("Clicked outside");
    });

    const onClickEvent = async (e: React.MouseEvent<HTMLButtonElement>) => {
        await processVideoAddition(
            "playlist",
            setCurrentVideo,
            videos,
            addVideo,
            user,
            togglePlaylist,
            duration,
            soundCloudVideo,
            tiktokVideo,
            youtubeVideo,
            clearVideos,
            pressed,
            setPressed,
            setShowTooltips,
        );
    };

    return (
        <StyledWrapper>
            <button className="btn" title="Add to Playlist" onClick={onClickEvent}>
                <QueueMusicIcon className="icon" />
            </button>

            {showTooltips && (
                <div ref={clickRef}>
                    <Tooltips />
                </div>
            )}
        </StyledWrapper>
    );
};

const wiggle = keyframes`
    0%, 100% { transform: rotate(0deg); }
    25% { transform: rotate(-10deg); }
    50% { transform: rotate(10deg); }
`;

const StyledWrapper = styled.div`
    .btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 10px;
        border: none;
        background-color: transparent;
        position: relative;
        cursor: pointer;
        transition: transform 0.2s ease-in-out;
        padding: 0;
    }

    .btn::after {
        content: "Add to Playlist";
        white-space: nowrap;
        position: absolute;
        font-size: 15px;
        color: white;
        font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
        opacity: 0;
        visibility: hidden;
        transition:
            opacity 0.2s linear,
            top 0.2s linear;
        top: 115%; /* Position the text below the button */
    }

    .btn:hover::after {
        opacity: 1;
        visibility: visible;
        top: 105%; /* Position adjustment on hover */
    }

    .icon {
        color: white;
        width: 35px;
        height: 35px;
        transition:
            color 0.4s ease-in-out,
            transform 0.4s ease-in-out;
    }

    .btn:hover > .icon {
        fill: #ff0000; /* Red on hover */
        transform: scale(1.2); /* Scale effect */
    }

    .btn:active > .icon {
        animation: ${wiggle} 0.8s ease; /* Wiggle effect on click */
    }
`;

export default PlaylistButton;
