import { toast } from "sonner";
import { Player } from "../interfaces";
import { addVideoToPlaylist } from "@/app/firebase";
import { User } from "firebase/auth";
import createVideoObject from "./createVideoObject";
import { containsVideo } from "./containsVideo";

export default async function processVideoAddition(
    buttonType: "queue" | "playlist",
    setCurrentVideo: (video: Player) => void,
    videos: Player[],
    addVideo: (video: Player) => void,
    user: User | null,
    togglePlaylist: (enabled: boolean) => void,
    duration: number,
    soundCloudVideo: Player,
    youtubeVideo: Player,
    tiktokVideo: Player,
    clearVideos: () => void,
    pressed: boolean,
    setPressed: React.Dispatch<React.SetStateAction<boolean>>,
    setShowTooltips?: React.Dispatch<React.SetStateAction<boolean>>,
) {
    const video = createVideoObject(duration, tiktokVideo, youtubeVideo, soundCloudVideo);
    if (!video) return;

    if (containsVideo(videos, video)) return toast.error("Video is already in the playlist");

    if (buttonType === "playlist") {
        console.log('running playlist process')
        if(!pressed) clearVideos();
        setPressed(true)
        console.log('clearVideos was ran')
        togglePlaylist(true);

        if (user) {
            await addVideoToDB(user.uid, video);
            addVideoToArray(setCurrentVideo, addVideo, video);
            toastMessage(buttonType);
        } else {
            if (setShowTooltips) {
                setShowTooltips(true);
                return;
            }
        }
    } else if (buttonType === "queue") {
        console.log("running queue process");
        if(!pressed) clearVideos();
        setPressed(true)
        console.log('cearVideos was ran')
        togglePlaylist(false);
        addVideoToArray(setCurrentVideo, addVideo, video);
        toastMessage(buttonType);
    }
}

async function addVideoToDB(userID: string, video: Player) {
    const result = await addVideoToPlaylist(userID, video);
    if (result.error && result.error === "Video already exists in the playlist") {
        toast.error("Video already exists in the playlist");
        return null;
    }
    return result.playlistID;
}

function addVideoToArray(setCurrentVideo: (video: Player) => void, addVideo: (video: Player) => void, video: Player) {
    addVideo(video);
    setCurrentVideo(video);
}
function toastMessage(buttonType: "queue" | "playlist") {
    switch (buttonType) {
        case "queue":
            toast.success("Video added to the queue");
            break;
        case "playlist":
            toast.success("Video added to the playlist");
            break;
        default:
            break;
    }
}
