import { useEffect } from "react";

export default function useClickOutside(
    clickRef: React.RefObject<HTMLDivElement | null>,
    setState: React.Dispatch<React.SetStateAction<boolean>>,
    eventTypes: Array<"click" | "mousedown"> = ["click"], // Specify the event types to listen for
    onOutsideClick?: () => void // Optional callback for custom behavior on outside click
) {
    const handleClickOutside = (e: MouseEvent) => {
        if (clickRef.current && !clickRef.current.contains(e.target as Node)) {
            setState(false);
            if (onOutsideClick) {
                onOutsideClick();
            }
        }
    };

    useEffect(() => {
        const eventListener = (e: MouseEvent) => handleClickOutside(e);

        eventTypes.forEach((eventType) => {
            document.addEventListener(eventType, eventListener, true);
        });

        return () => {
            eventTypes.forEach((eventType) => {
                document.removeEventListener(eventType, eventListener, true);
            });
        };
    }, [eventTypes, clickRef]);
}
