import createNewVideo from "./createNewVideo";
import getYoutubeVideoDetails from "./getYoutubeVideoDetails";
import { ICreateVideo, Player } from "../interfaces";
import fetchTiktokVideoDetails from "./fetchTiktokVideoDetails";

export default async function createVideo({ videoID, platform, setYoutubeVideo, setTiktokVideo, setSoundCloudVideo, optionals }: ICreateVideo) {
    switch (platform) {
        case "youtube":
            if (typeof setYoutubeVideo === "undefined" || !setYoutubeVideo) return;

            const youtubeResults = await getYoutubeVideoDetails(videoID);
            if (!youtubeResults) return;

            console.log(youtubeResults.creator);

            const requiredYoutubeVideoDetails: Player = {
                videoID: videoID,
                title: youtubeResults.title,
                platform: "youtube",
                creator: youtubeResults.creator,
                thumbnail: youtubeResults.thumbnail,
                url: `https://www.youtube.com/watch?v=${videoID}`,
            };

            const newYoutubeVideo = createNewVideo(requiredYoutubeVideoDetails, optionals);
            if (newYoutubeVideo) setYoutubeVideo(newYoutubeVideo);
            break;

        case "tiktok":
            if (typeof setTiktokVideo === "undefined" || !setTiktokVideo) return;

            const tiktokVideoURL = `https://www.tiktok.com/@${videoID.split("+")[0]}/video/${videoID.split("+")[1]}`;
            const tiktokResults = await fetchTiktokVideoDetails(tiktokVideoURL);
            if (tiktokResults.error) return null;
            const { creator, title, thumbnail } = tiktokResults.data;
            const creatorAndID = `${creator}+${videoID}`;
            const requiredTiktokVideoDetails: Player = {
                title,
                creator,
                thumbnail,
                platform: "tiktok",
                url: tiktokVideoURL,
                videoID,
            };
            const newTiktokVideo = createNewVideo(requiredTiktokVideoDetails, optionals);
            if (newTiktokVideo) setTiktokVideo(newTiktokVideo);
            break;

        default:
            break;
    }
}
