export default function getPlatform(url: string) {
    const supportedPlatforms: { regex: RegExp; name: string }[] = [
        { regex: /youtu\.be/i, name: "Youtu.be" },
        { regex: /tiktok\.com/i, name: "Tiktok" },
        { regex: /youtube\.com/i, name: "Youtube" },
        { regex: /twitter\.com/i, name: "Twitter" },
        { regex: /instagram\.com/i, name: "Instagram" },
        { regex: /soundcloud\.com/i, name: "SoundCloud" },
    ];

    for (const platform of supportedPlatforms) {
        if (platform.regex.test(url)) {
            if (platform.name === "Youtube" && url.includes("shorts")) return "Youtube Shorts";
            return platform.name;
        }
    }
    return null;
}
