import getVideoID from "./getVideoID";
import getPlatform from "./getPlatform";
import getYoutubeVideoDetails from "./getYoutubeVideoDetails";
import fetchTiktokVideoDetails from "./fetchTiktokVideoDetails";
import fetchTrackDetails from "./fetchTrackDetails";

export default async function getVideoDetails(url: string) {
    const platform = getPlatform(url);
    if (!platform) return null;

    const videoID = getVideoID(platform, url);
    if (!videoID) return null;

    const PlayerDetails: IPlayerDetails = {
        tiktokVideoDetails: null,
        youtubeVideoDetails: null,
        soundCloudVideoDetails: null,
    };

    if (platform === "Youtube") {
        const result = await getYoutubeVideoDetails(videoID);
        if (result.error) return null;

        const { thumbnail, title, creator } = result;
        return {
            ...PlayerDetails,
            youtubeVideoDetails: {
                title,
                videoID,
                thumbnail,
                creator,
                url: `https://www.youtube.com/watch?v=${videoID}`,
            },
        };
    } else if (platform === "Tiktok") {
        const result = await fetchTiktokVideoDetails(url);
        if (result.error) return null;

        const { creator, title, thumbnail } = result.data;
        const creatorAndID = `${creator}+${videoID}`;

        return {
            ...PlayerDetails,
            tiktokVideoDetails: {
                url,
                title,
                creator,
                thumbnail,
                videoID: creatorAndID,
            },
        };
    } else if (platform === "SoundCloud") {
        const result = await fetchTrackDetails(url);
        if (result.error) return null;

        const { creator, thumbnail, title, trackID } = result;
        return {
            ...PlayerDetails,
            soundCloudVideoDetails: {
                url,
                title,
                creator,
                thumbnail,
                videoID: trackID,
            },
        };
    }
}

interface IPlayerDetails {
    tiktokVideoDetails: null;
    youtubeVideoDetails: null;
    soundCloudVideoDetails: null;
}
