import { toast } from "sonner";
import { usePlayer, useMedia, useUser } from "@/app/store";
import styled, { keyframes } from "styled-components";
import { containsVideo, processVideoAddition, createVideoObject } from "@/app/utils";
import { useEffect, useState } from "react";

const QueueButton = () => {
    const [pressed, setPressed] = useState<boolean>(false)
    const { user, togglePlaylist } = useUser();
    const { tiktokVideo, soundCloudVideo, youtubeVideo, duration } = usePlayer();
    const { videos, addVideo, setCurrentVideo, clearQueue, clearVideos } = useMedia();

    const onClickEvent = async (e: React.MouseEvent<HTMLButtonElement>) => {
        await processVideoAddition(
            "queue",
            setCurrentVideo,
            videos,
            addVideo,
            user,
            togglePlaylist,
            duration,
            soundCloudVideo,
            youtubeVideo,
            tiktokVideo,
            clearVideos,
            pressed,
            setPressed
        );
    };

    useEffect(() => {
        console.log(videos)
    }, [videos])

    return (
        <StyledWrapper>
            <button className="btn" title="Add New" onClick={onClickEvent}>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" className="icon">
                    <path d="M12 22C17.5 22 22 17.5 22 12C22 6.5 17.5 2 12 2C6.5 2 2 6.5 2 12C2 17.5 6.5 22 12 22Z" strokeWidth="1.5" />
                    <path d="M8 12H16" strokeWidth="1.5" />
                    <path d="M12 16V8" strokeWidth="1.5" />
                </svg>
            </button>
        </StyledWrapper>
    );
};

const wiggle = keyframes`
    0%, 100% { transform: rotate(0deg); }
    25% { transform: rotate(-10deg); }
    50% { transform: rotate(10deg); }
`;

const StyledWrapper = styled.div`
    .btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 10px;
        border: none;
        background-color: transparent;
        position: relative;
        cursor: pointer;
        transition: transform 0.2s ease-in-out;
        margin-left: 20px; /* Adjust margin as needed */
        padding: 0; /* Reset padding for a consistent look */
    }

    .btn::after {
        content: "Add To Queue";
        white-space: nowrap;
        position: absolute;
        font-size: 15px;
        color: white;
        font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
        opacity: 0;
        visibility: hidden;
        transition:
            opacity 0.2s linear,
            top 0.2s linear;
        top: 115%; /* Position the text below the button */
    }

    .btn:hover::after {
        opacity: 1;
        visibility: visible;
        top: 105%; /* Position adjustment on hover */
    }

    .icon {
        stroke: #fff;
        fill: none;
        width: 35px;
        height: 35px;
        transition:
            fill 0.4s ease-in-out,
            transform 0.4s ease-in-out;
    }

    .btn:hover > .icon {
        fill: #ff0000; /* Red fill on hover */
        transform: rotate(90deg); /* Rotate effect */
    }

    .btn:active > .icon {
        fill: #00d1b2; /* Teal fill on active */
        animation: ${wiggle} 0.5s ease; /* Wiggle effect on click */
    }
`;

export default QueueButton;
