export async function getTikTokVideoDetails(url: string) {
    try {
        const apiUrl = `https://www.tiktok.com/oembed?url=${encodeURIComponent(url)}`;

        const response = await fetch(apiUrl);

        if (!response.ok) {
            throw new Error(`Failed to fetch TikTok metadata: ${response.statusText}`);
        }

        const data = await response.json();

        return {
            success: true,
            error: null,
            title: data.title,
            creator: data.author_unique_id,
            thumbnail: data.thumbnail_url,
        };
    } catch (error) {
        return {
            success: false,
            error: (error as Error).message,
            title: null,
            creator: null,
            thumbnail: null,
        };
    }
}
