import { usePlayer } from "@/app/store";
import { useEffect, useState } from "react";
import { createSearchParams, Player, getVideoDetails } from "@/app/utils";

export default function Input() {
    const [url, setUrl] = useState<string>("");
    const { setYoutubeVideo, setTiktokVideo } = usePlayer();

    useEffect(() => {
        if (url.length < 10) return;

        console.log(url);

        fetchVideoData(url, setYoutubeVideo, setTiktokVideo);
    }, [url]);

    return (
        <div className="relative mb-4 h-12 w-full">
            <input
                type="text"
                value={url}
                autoComplete="off"
                onChange={(e) => setUrl(e.target.value)}
                placeholder="https://www.youtube.com/watch?v=FfsINwUQ75Q"
                className="h-full w-full rounded-lg border-2 border-gray-200 bg-white px-6 py-2 text-black placeholder-gray-500 outline-none"
            />
        </div>
    );
}
async function fetchVideoData(url: string, setYoutubeVideo: (youtubeVideo: Player) => void, setTiktokVideo: (tiktokVideo: Player) => void) {
    let newSearchParams: Record<string, string> = {};
    const result = await getVideoDetails(url);
    let videoDetails: any = null;
    if (!result) return;

    if (result.youtubeVideoDetails) {
        videoDetails = result.youtubeVideoDetails;
        newSearchParams = { yVideoID: videoDetails.videoID };
    } else if (result.tiktokVideoDetails) {
        videoDetails = result.tiktokVideoDetails;
        newSearchParams = { tVideoID: videoDetails.videoID };
    }

    if (videoDetails) {
        console.log("running in Input.tsx", videoDetails, newSearchParams);
        
        if(result.youtubeVideoDetails) {
            setTiktokVideo(null)
            setYoutubeVideo(videoDetails);
        }
        else if(result.tiktokVideoDetails) {
            setYoutubeVideo(null)
            setTiktokVideo(videoDetails);
        }
        createSearchParams(newSearchParams);
    }
}
