import { 
    sanitizeTiktokURL,
    sanitizeYoutubeURL,
    sanitizeYoutubeShortsURL,
    sanitizeEmbeddedYoutubeURL,
 } from './sanitizeURLs'

export default function getVideoID(platform: string, url: string) {
    if (platform === "Youtube") return sanitizeYoutubeURL(url)
    else if (platform === 'Youtube Shorts') return sanitizeYoutubeShortsURL(url)
    else if (platform === 'Youtu.be') return sanitizeEmbeddedYoutubeURL(url)
    else if (platform === 'Tiktok') return sanitizeTiktokURL(url)
    else if (platform === 'SoundCloud') return 'SoundCloud'
    else return null
}