import React from "react";
import { useMedia } from "@/app/store";
import ShuffleIcon from "@mui/icons-material/Shuffle";
import styled, { keyframes } from "styled-components";

const Shuffle = () => {
    const { toggleLoop } = useMedia();

    const onShuffle = () => toggleLoop(false);

    return (
        <StyledWrapper>
            <button className="btn" onClick={onShuffle}>
                <ShuffleIcon className="icon" />
            </button>
        </StyledWrapper>
    );
};

const wiggle = keyframes`
    0%, 100% { transform: rotate(0deg); }
    25% { transform: rotate(-10deg); }
    50% { transform: rotate(10deg); }
`;

const StyledWrapper = styled.div`
    .btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        border-radius: 10px;
        border: none;
        background-color: transparent;
        position: relative;
        cursor: pointer;
        transition: transform 0.2s ease-in-out;
        margin-left: 20px; /* Adjust margin as needed */
    }

    .btn::after {
        content: "Shuffle Videos";
        white-space: nowrap;
        position: absolute;
        font-size: 15px;
        color: white;
        font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.2s linear, top 0.2s linear;
        top: 115%; /* Position the text below the button */
    }

    .btn:hover::after {
        opacity: 1;
        visibility: visible;
        top: 105%; /* Position adjustment on hover */
    }

    .icon {
        fill: black;
        transition: fill 0.4s ease-in-out;
        height: 28px;
        width: 28px;
    }

    .btn:hover > .icon {
        transform: scale(1.2); /* Scale effect on hover */
        fill: rgb(255, 255, 255);
    }

    .btn:active > .icon {
        animation: ${wiggle} 0.5s ease; /* Wiggle effect on click */
    }
`;

export default Shuffle;
