function sanitizeTiktokURL(url: string) {
    const match = url.match(/\/video\/(\d+)/); // Regex to match the video ID
    return match ? match[1] : null;
}
function sanitizeYoutubeShortsURL(url: string) {
    const match = url.match(/\/shorts\/([\w-]+)/)
    return match ? match[1] : null
}
function sanitizeYoutubeURL(url: string): string | null {
    const match = url.match(/(?:v=)([^"&?/\s]{11})/)
    return match ? match[1] : null
}
function sanitizeEmbeddedYoutubeURL(url: string): string | null {
    const match = url.match(/youtu\.be\/([^"&?/\s]{11})/)
    return match ? match[1] : null
}

export {
    sanitizeTiktokURL,
    sanitizeYoutubeURL,
    sanitizeYoutubeShortsURL,
    sanitizeEmbeddedYoutubeURL,
}