export async function sendVerificationEmail(email: string) {
    try {
        const response = await fetch("/api/emails/verification", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({ email }),
        });

        if (response.ok) {
            const { data, code, expirationDate } = await response.json();
            return { success: true, data, code, expirationDate };
        } else {
            const errorData = await response.json();
            return { success: false, error: errorData.error || "Failed to send email" };
        }
    } catch (error) {
        return { success: false, error: (error as Error).message };
    }
}
