"use client";

import UI from "./ui";
import { useEffect, useRef } from "react";
import YoutubePlayer from "react-player/youtube";
import { usePlayer, useClient, useUser } from "@/app/store";
import SoundCloudPlayer from "react-player/soundcloud";
import { createSearchParams, setNewPlayerVideo } from "@/app/utils";
import Guide from "./Guide";

export default function VideoPlayer() {
    const { initializeClient, isClient } = useClient();
    const youtubePlayerRef = useRef<YoutubePlayer | null>(null);
    const soundCloudPlayerRef = useRef<SoundCloudPlayer | null>(null);

    const { duration, tiktokVideo, sliderValue, youtubeVideo, setSliderValue, setTiktokVideo, soundCloudVideo, setYoutubeVideo, setSoundCloudVideo } =
        usePlayer();

    // Fetch url params
    // const params = new URLSearchParams(window.location.search);
    // const indexParam = params.get("index");
    // const listParam = params.get("list");

    useEffect(() => {
        initializeClient();
    }, [initializeClient]);

    useEffect(() => {
        const unsubscribe = useUser.getState().subscribeToAuthState();
        return () => unsubscribe();
    }, []);

    useEffect(() => {
        if (typeof window === "undefined") return;

        const params = new URLSearchParams(window.location.search);
        const indexParam = params.get("index");
        const listParam = params.get("list");

        if (sliderValue[0] >= 0 && duration > 0) {
            const player = youtubeVideo || tiktokVideo || soundCloudVideo;
            const videoRef = youtubePlayerRef.current || soundCloudPlayerRef.current;
            const timeoutId = setTimeout(() => {
                if (videoRef) videoRef.seekTo(sliderValue[0]);

                if (player) {
                    let newSearchParams: Record<string, string> = {};
                    var list = youtubeVideo?.list || listParam;
                    var index = player.index || indexParam;
                    if (player === youtubeVideo) {
                        if (index && list) {
                            newSearchParams = {
                                yVideoID: youtubeVideo.videoID,
                                start: sliderValue[0].toString(),
                                end: sliderValue[1].toString(),
                                list,
                                index,
                            };
                        } else {
                            newSearchParams = {
                                yVideoID: youtubeVideo.videoID,
                                start: sliderValue[0].toString(),
                                end: sliderValue[1].toString(),
                            };
                        }
                    } else if (player === tiktokVideo) {
                        if (index && list) {
                            newSearchParams = {
                                tVideoID: tiktokVideo.videoID,
                                list,
                                index,
                            };
                        } else {
                            newSearchParams = { tVideoID: tiktokVideo.videoID };
                        }
                    }
                    createSearchParams(newSearchParams);
                }
            }, 300);
            return () => clearTimeout(timeoutId);
        }
    }, [sliderValue, duration]);

    useEffect(() => {
        setNewPlayerVideo(setTiktokVideo, setYoutubeVideo, setSliderValue, setSoundCloudVideo);
    }, []);

    if (!isClient) return; // return loading spinner

    return (
        <>
            {isClient && (
                <>
                    <UI youtubeVideo={youtubeVideo} tiktokVideo={tiktokVideo} youtubePlayerRef={youtubePlayerRef} />
                    <Guide />
                </>
            )}
        </>
    );
}
