import { formatTime } from "@/app/utils";
import Slider from "@mui/material/Slider";
import { usePlayer } from "@/app/store/usePlayer";

export default function PlayerSlider() {
    const { duration, sliderValue, setSliderValue } = usePlayer();

    const onSliderChange = (event: Event, value: number | number[]) => {
        setSliderValue(value as [number, number]);
    };
    return (
        <>
            <Slider
                min={0}
                max={duration}
                value={sliderValue}
                onChange={onSliderChange}
                valueLabelDisplay="auto"
                valueLabelFormat={(value) => formatTime(value)}
                sx={{ color: "#ffffff" }}
            />
            <div className="w-full flex items-center justify-between mt-2">
                <span className="text-sm text-white">{formatTime(sliderValue[0])}</span>
                <span className="text-sm text-white">{formatTime(sliderValue[1])}</span>
            </div>
        </>
    );
}
