import { Player } from "@/app/utils";

function containsVideo(videos: Player[], newVideo: Player): boolean {
    return videos.some((video) => (video && video.url) === (newVideo && newVideo.url));
}

function containsVideoID(videos: Player[], videoID: string) {
    const index = videos.findIndex((video) => (video && video.videoID) === videoID);
    return {
        video: index !== -1 ? videos[index] : null,
        index: index !== -1 ? index : null,
    };
}

export {
    containsVideo,
    containsVideoID,
}