import { toast } from "sonner";
import { useEffect } from "react";
import PlayerSlider from "./Slider";
import ReactPlayer from "react-player/youtube";
import { useMedia, usePlayer, useUser } from "@/app/store";
import { createSearchParams, Player } from "@/app/utils";

export default function YoutubePlayer({ youtubePlayerRef, youtubeVideo }: { youtubePlayerRef: any; youtubeVideo: Player }) {
    const { resetSlider, setYoutubeVideo, setTiktokVideo, setSliderValue, setDuration, sliderValue } = usePlayer();
    const { setVideoIsEnded, playNextVideo, loopEnabled, playlistID } = useMedia();
    const { isPlaylistActive } = useUser();

    const url = youtubeVideo?.url;

    useEffect(() => {
        resetSlider();
        setVideoIsEnded(false);
    }, [youtubeVideo?.url, resetSlider]);

    const handleDuration = (videoLength: number) => {
        setDuration(videoLength);
        setSliderValue([0, videoLength]);
    };

    const handleProgress = (progress: { playedSeconds: number }) => {
        const tolerance = 1;

        if (
            (progress.playedSeconds >= sliderValue[1] - tolerance && progress.playedSeconds <= sliderValue[1] + tolerance) ||
            progress.playedSeconds >= sliderValue[1]
        ) {
            if (loopEnabled) {
                youtubePlayerRef.current.seekTo(sliderValue[0]); // Seek to the loop start
            } else {
                setVideoIsEnded(true);
                playNextVideo(setTiktokVideo, setYoutubeVideo, setSliderValue, isPlaylistActive, playlistID, createSearchParams);
            }
        }
    };

    if (!youtubeVideo) return;

    return (
        <>
            <ReactPlayer
                playing
                controls
                url={url}
                width="100%"
                height="100%"
                ref={youtubePlayerRef}
                onDuration={handleDuration}
                onProgress={handleProgress}
                onError={() => {
                    toast.error("Error loading video, please check the URL.");
                }}
            />
            <PlayerSlider />
        </>
    );
}
