import Loop from "./Loop";
import Queue from "./Queue";
import Input from "./Input";
import { Toaster } from "sonner";
import Playlist from "./Playlist";
import { useEffect } from "react";
import { Player } from "@/app/utils";
import Shuffle from "./ShufftleVideos";
import QueueButton from "./QueueButton";
import YoutubePlayer from "./YoutubePlayer";
import PlaylistButton from "./PlaylistButton";
import { useMedia, useUser } from "@/app/store";
import TiktokPlayer from "./TiktokPlayer";

export default function UI({ youtubeVideo, tiktokVideo, youtubePlayerRef }: { youtubeVideo: Player; tiktokVideo: Player; youtubePlayerRef: any }) {
    const { isPlaylistActive } = useUser();
    const { clearQueue } = useMedia();

    useEffect(() => {
        console.log(isPlaylistActive)
        clearQueue();
    }, [isPlaylistActive]);

    return (
        <>
            <section className="mt-16 relative flex min-h-screen bg-gradient-to-t from-teal-200 via-teal-100 to-teal-50">
                <div className="flex-1 flex flex-col 2xl:flex-row justify-center items-start mt-4 mx-4 space-y-8 2xl:space-y-0 2xl:space-x-8">
                    {/* Video Player Section */}
                    <div className="relative h-[850px] w-full 2xl:w-[1000px] flex flex-col justify-between items-center bg-transparent rounded-lg shadow-lg p-6">
                        <Input />

                        {/* Video Players */}
                        <YoutubePlayer youtubeVideo={youtubeVideo} youtubePlayerRef={youtubePlayerRef} />
                        <TiktokPlayer tiktokVideo={tiktokVideo} />

                        {/* Player Buttons */}
                        <div className="rounded-lg flex items-center justify-between mt-2 space-x-20 mb-4">
                            <Loop />
                            <PlaylistButton />
                            <QueueButton />
                            <Shuffle />
                        </div>
                    </div>

                    {/* Playlist Section */}
                    <div className="relative h-[850px] w-full 2xl:w-[500px] flex flex-col justify-between items-center bg-transparent rounded-lg shadow-lg p-6">
                         <Playlist />
                        {/* <Queue isPlaylistActive={isPlaylistActive} />    */}
                    </div>
                </div>

                {/* Toast Notification */}
                <div className="fixed bottom-8 left-8 z-50 w-auto max-w-xs">
                    <Toaster duration={5000} richColors />
                </div>
            </section>
        </>
    );
}
